body {
  background-color: rgb(26, 26, 26);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: auto;
}

#root {
  justify-content: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.circle {
  background-color: #fff;
  color: rgb(26, 26, 26);
  border-radius: 50%;
  width: 100px;
  height: 100px;
  vertical-align: middle;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
